@import url('https://fonts.googleapis.com/css2?family=Inter&family=Karla:wght@400;700&display=swap');

*, *::before, *::after {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: inherit;
}

html {
    display: flex;
    flex-direction: column;
    min-height: 100%;
}

body {
    font-family: 'Karla', sans-serif;
    flex-grow: 1;
    background-color: #0B2434;
    display: flex;
    justify-content: center;
    align-items: center;
}

.main {
    position: relative;
    width: 322px;
    height: 340px;
    padding: 35px;
    background-color: #F5F5F5;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}

.die {
    font-size: 21px;
    font-weight: 700;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 36px;
    height: 36px;
    box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.15);
    border-radius: 3.81277px;
    position: relative;
    background-repeat: no-repeat;
    background-position: center;
    background-size: 140%;
}

.die::before {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 100;
}

.dices {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 18px;
    /*margin-top: 15px;*/
    /*margin-bottom: 25px;*/
}

.description {
    font-family: 'Inter', sans-serif;
    max-width: 230px;
    text-align: center;
    font-size: 13.1072px;
    line-height: 16px;
    letter-spacing: -0.03em;
    height: 60px;
    color: #4A4E74;
}

.button {
    font-weight: 700;
    font-size: 16.384px;
    line-height: 19px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    color: #FFFFFF;
    background: #5035FF;
    box-shadow: 0px 3.2px 7.68px rgba(0, 0, 0, 0.18);
    border-radius: 3.81277px;
    border: none;
    width: 110px;
    height: 36px;
    cursor: pointer;
    transition: background-color .2s;
    outline: none;
    margin-top: 10px;
}

.button:hover {
    background: #2c1ea2;
}

.button:active {
    background: #6857e8;
}

.dice-1 {
    background-image: url("./images/dice-1.svg");
}

.dice-2 {
    background-image: url("./images/dice-2.svg");
}

.dice-3 {
    background-image: url("./images/dice-3.svg");
}

.dice-4 {
    background-image: url("./images/dice-4.svg");
}

.dice-5 {
    background-image: url("./images/dice-5.svg");
}

.dice-6 {
    background-image: url("./images/dice-6.svg");
}

.rolls-count {
    /*flex-grow: 1;*/
    height: 60px;
    font-size: 30px;
    font-weight: 700;
    /*margin-top: 10px;*/
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 5px;
}

.best {
    display: block;
    font-size: 14px;
}

.time-count {
    width: 40px;
    display: flex;
    justify-content: center;
}

.time-container {
    position: absolute;
    right: 20px;
    top: 20px;
    display: flex;
    flex-direction: column;
    gap: 3px;
}

.results {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.results__rolls {
    margin-bottom: 5px;
}

.results__rolls,
.results__time {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.results__title {
    font-size: 30px;
    font-weight: 700;
    margin-bottom: 4px;
}

.rolls, .time {
    font-size: 18px;
    font-weight: 700;
}

.rolls-best, .time-best {
    font-size: 14px;
}

.rolls-amount,
.time-amount {
    font-size: 20px;
}

.buttons {
    display: flex;
    gap: 20px;
}

.wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 6px;
}

.button-shake {
    animation: shake 1s ease-in-out forwards;
}

@keyframes shake {
    0%, 100% {
        transform: translate(0, 0);
    }
    10%, 30%, 50%, 70%, 90% {
        transform: translate(3px);
    }
    20%, 40%, 60%, 80% {
        transform: translate(-3px);
    }
}
